<template>
  <section class="products-link">
    <form action="">
      <div class="box-search">
        <input
          type="text"
          v-model="searchQuery"
          placeholder="Tìm kiếm danh mục ..."
        />
        <i class="fa-solid fa-magnifying-glass"></i>
      </div>
    </form>


      <div class="total-products">
        <h3> Số lượng danh mục hiện có: ({{resultQuery.length}})</h3>
      </div>

    
      <div class="box-container">
        
        <carousel
        :autoplay="true"
        :autoHeight="true"
        :margin="10"
        :responsive="{
            0:{items:2,nav:false},
            600:{items:3,nav:true},
            900:{items:4.3,nav:true}
          }"
        >
          <div
            v-for="(item, index) in resultQuery"
            :key="`item-${index}`"
            :class="item.cls"
          >
            <div class="image-products">
              <div class="image-img">
                  <img :src="item.img_products" alt="" />
              </div>

              <div class="ic-image">
                <img class="ic-1" :src="item?.ic_1" alt="">
                <img class="ic-2" :src="item?.ic_2" alt="">
                <img class="ic-3" :src="item?.ic_3" alt="">
                <img class="ic-3" :src="item?.ic_4" alt="">
              </div>
            </div>

            <div class="content-products">
              <h3>Web: {{ item.name }}</h3>
              <a :href="item.href">xem liên kết</a>
            </div>

            
          </div>
        </carousel>

      </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import carousel from 'vue-owl-carousel'

export default {
  name: "comp-details",

    components:{
      carousel 
    },

    data() {
        return {
            searchQuery:"",
        }
    },
    
    computed:{
      ...mapGetters(["products"]),

      resultQuery() {
        if(this.searchQuery) {
          return this.products.filter((item) =>{
            return this.searchQuery
            .toLowerCase()
            .split("")
            .every((v) => item.name.toLowerCase().includes(v))
          })
        }else{
          return this.products
        }
      },
    },

    methods: {
      ...mapActions(["getProducts"])
    },

    mounted() {
      return this.getProducts();
    }
};
</script>

<style lang="scss">
.products-link {
  
  form {
    margin: 2rem 0 20rem 0;

    .box-search {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 2rem;
      border-radius: 5rem;
      margin: auto;
      width: 90%;
      max-width: 60rem;
      height: 4rem;
      border: .1rem solid #eeee;

      input {
        width: 100%;
        max-width: 90%;
        outline: none;
        border-radius: 5rem;
        background: var(--light-search);
        font-weight: 400;
        border: none;
        font-size: 16px;
        color: #fff;
        
        &::placeholder{
          font-size: 1.4rem;
          color: #fff;
        }
      }

      i {
        padding: 0.5rem;
        font-size: 1.5rem;
        color: #fff;

        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  .total-products{
    h3{
      color: #fff;
      font-size: 1.6rem;
    }
  }

  .box-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(32rem, 1fr));
    
    .box {
      background: var(--bg-box);
      box-shadow:var(--box-shadow);
      padding: 2rem;
      margin-top: .5rem;
      border-radius: 1rem;
      animation: showed .5s ease-out ;

      .image-products {
        border-bottom: 0.4rem solid #cfcfcf;
        position: relative;
        overflow: hidden;

        .image-img{
           img {
            width: 100%;
          }
        }

        .ic-image{
          position: absolute;
          bottom: -100%;
          left: 1rem;
          display: flex;
          margin: .5rem 0;

          .ic-1,
          .ic-2,
          .ic-3,
          .ic-4{
            margin: 0 .5rem;
            width: 2rem;
          }

          .ic-1{
            margin-left: 0;
          }

          .ic-4{
            margin-right: 0;
          }
        }
      }

      .content-products {
        h3 {
          font-size: 1.7rem;
          font-weight: 650;
          color: var(--black);
        }

        a {
          padding: 0.5rem 1.7rem;
          font-size: 1.7rem;
          color: #fff;
          background: var(--link-button);
          border-radius: 5rem;

          &:hover {
            transition: 0.3s linear;
            letter-spacing: 0.1rem;
          }
        }
      }

      

      &:hover{
        transition: .3s linear;
        transform: translateY(-.7rem);

        .image-products{
          overflow: hidden;
          .ic-image{
            transition: .3s ease;
            bottom: 0;
            padding: .5rem 2rem;
            background: var(--white);
            overflow: hidden;
            border-radius: 5rem;
            box-shadow: var(--box-shadow);
           
            
            .ic-1{
              animation: fadeup .45s linear;
            }

            .ic-2{
              animation: fadeup .55s linear;
            }

            .ic-3{
              animation: fadeup .65s linear;
            }

            .ic-4{
              animation: fadeup .7s linear;
            }
          }
        }
      }

        
    }
  }
}


@keyframes fadeup{
  0%{
    transform: translateY(-3rem);
  }

  100%{
    transform: translateY(0);
  }
}

@keyframes showed{
  0%{
    opacity: 0;
    transform: scale(0);
  }

  100%{
    opacity: 1;
    transform: scale(1);
  }
}

</style>
